export default {
	props:{
		video: {
			type: String
		},
		show: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			src: '',
			videoContext: '',
			CustomBar: this.CustomBar,
		}
	},
	mounted() {
		this.videoContext = uni.createVideoContext('video')
	},
	methods: {
		hide: function() {
			this.$emit('hide')
		}
	},
	watch: {
		show(a) {
			if(a) {
				this.videoContext.play()
			} else {
				this.videoContext.stop()
			}
		}
	}
}
